"use client";

import React, { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Share2, Plus, Quote } from "lucide-react";
import { useIsMobile } from "@/hooks/use-mobile";

interface Quote {
  id: string;
  text: string;
  author?: string;
  date: string;
}

export default function QuotesApp() {
  const [quotes, setQuotes] = useState<Quote[]>([]);
  const [newQuote, setNewQuote] = useState("");
  const [newAuthor, setNewAuthor] = useState("");
  const [showAddForm, setShowAddForm] = useState(false);
  const [error, setError] = useState("");
  const isMobile = useIsMobile();

  // Load quotes from localStorage on mount
  useEffect(() => {
    try {
      const stored = localStorage.getItem("quotesApp");
      if (stored) {
        const parsedQuotes = JSON.parse(stored);
        setQuotes(parsedQuotes);
      }
    } catch (error) {
      console.error("Failed to load quotes:", error);
    }
  }, []);

  // Save quotes to localStorage whenever quotes change
  useEffect(() => {
    try {
      localStorage.setItem("quotesApp", JSON.stringify(quotes));
    } catch (error) {
      console.error("Failed to save quotes:", error);
    }
  }, [quotes]);

  const addQuote = () => {
    if (!newQuote.trim()) {
      setError("Quote text is required");
      return;
    }

    const quote: Quote = {
      id: Date.now().toString(),
      text: newQuote.trim(),
      author: newAuthor.trim() || undefined,
      date: new Date().toLocaleDateString(),
    };

    setQuotes([quote, ...quotes]);
    setNewQuote("");
    setNewAuthor("");
    setShowAddForm(false);
    setError("");
  };

  const shareQuote = async (quote: Quote) => {
    const shareText = quote.author 
      ? `"${quote.text}" - ${quote.author}`
      : `"${quote.text}"`;

    try {
      if (navigator.share) {
        await navigator.share({
          title: "Inspiring Quote",
          text: shareText,
        });
      } else if (navigator.clipboard) {
        await navigator.clipboard.writeText(shareText);
        alert("Quote copied to clipboard!");
      } else {
        // Fallback for older browsers
        const textArea = document.createElement("textarea");
        textArea.value = shareText;
        document.body.appendChild(textArea);
        textArea.select();
        document.execCommand("copy");
        document.body.removeChild(textArea);
        alert("Quote copied to clipboard!");
      }
    } catch (error) {
      console.error("Error sharing quote:", error);
      alert("Unable to share quote");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-100 dark:from-gray-900 dark:to-gray-800">
      {/* Header */}
      <div className="bg-white dark:bg-gray-900 shadow-sm border-b">
        <div className="max-w-md mx-auto px-4 py-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <div className="p-2 bg-black dark:bg-white rounded-lg">
                <Quote className="h-6 w-6 text-white dark:text-black" />
              </div>
              <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
                Quotes
              </h1>
            </div>
            <Button
              onClick={() => setShowAddForm(!showAddForm)}
              size="sm"
              className="bg-black hover:bg-gray-800 text-white"
            >
              <Plus className="h-4 w-4 mr-1" />
              Add
            </Button>
          </div>
        </div>
      </div>

      <div className="max-w-md mx-auto px-4 py-6 space-y-6">
        {/* Add Quote Form */}
        {showAddForm && (
          <Card className="border-2 border-dashed border-gray-300 dark:border-gray-600">
            <CardHeader>
              <CardTitle className="text-lg">Add New Quote</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <Textarea
                  placeholder="Enter your quote here..."
                  value={newQuote}
                  onChange={(e) => setNewQuote(e.target.value)}
                  className="min-h-[100px] resize-none"
                />
              </div>
              <div>
                <Input
                  placeholder="Author (optional)"
                  value={newAuthor}
                  onChange={(e) => setNewAuthor(e.target.value)}
                />
              </div>
              {error && (
                <p className="text-sm text-red-500">{error}</p>
              )}
              <div className="flex space-x-2">
                <Button
                  onClick={addQuote}
                  className="flex-1 bg-black hover:bg-gray-800 text-white"
                >
                  Save Quote
                </Button>
                <Button
                  onClick={() => {
                    setShowAddForm(false);
                    setNewQuote("");
                    setNewAuthor("");
                    setError("");
                  }}
                  variant="outline"
                  className="flex-1"
                >
                  Cancel
                </Button>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Quotes List */}
        {quotes.length === 0 ? (
          <Card className="text-center py-12">
            <CardContent>
              <Quote className="h-12 w-12 mx-auto text-gray-400 mb-4" />
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
                No quotes yet
              </h3>
              <p className="text-gray-500 dark:text-gray-400 mb-4">
                Start building your collection of inspiring quotes
              </p>
              <Button
                onClick={() => setShowAddForm(true)}
                className="bg-black hover:bg-gray-800 text-white"
              >
                <Plus className="h-4 w-4 mr-2" />
                Add Your First Quote
              </Button>
            </CardContent>
          </Card>
        ) : (
          <div className="space-y-4">
            {quotes.map((quote) => (
              <Card
                key={quote.id}
                className="bg-white dark:bg-gray-800 shadow-sm hover:shadow-md transition-shadow duration-200"
              >
                <CardContent className="p-6">
                  <div className="mb-4">
                    <Quote className="h-6 w-6 text-gray-400 mb-2" />
                    <p className="text-lg leading-relaxed text-gray-900 dark:text-white font-medium">
                      {quote.text}
                    </p>
                    {quote.author && (
                      <p className="text-sm text-gray-600 dark:text-gray-400 mt-2 font-medium">
                        — {quote.author}
                      </p>
                    )}
                  </div>
                  <div className="flex items-center justify-between">
                    <span className="text-xs text-gray-500 dark:text-gray-400">
                      {quote.date}
                    </span>
                    <Button
                      onClick={() => shareQuote(quote)}
                      size="sm"
                      variant="outline"
                      className="flex items-center space-x-1"
                    >
                      <Share2 className="h-4 w-4" />
                      <span>Share</span>
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        )}

        {/* Stats */}
        {quotes.length > 0 && (
          <Card className="bg-gray-50 dark:bg-gray-800/50">
            <CardContent className="p-4 text-center">
              <p className="text-sm text-gray-600 dark:text-gray-400">
                You have collected{" "}
                <span className="font-bold text-black dark:text-white">
                  {quotes.length}
                </span>{" "}
                {quotes.length === 1 ? "quote" : "quotes"}
              </p>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  );
}
